import React from "react";
import {LifeStamp} from "../lib/stamps.utils";
import {StampsContext} from './contexts/StampsContext.contexts';
import ReactMarkdown from 'react-markdown';

export default function ListStamps() {

    return (
        <StampsContext.Consumer>
            {
                ({stamps}) => {
                    return <div className="stamps" key={'stamps'}>
                        {
                            stamps &&
                            stamps.length > 0 &&
                            stamps
                                .sort((a, b) => b.created_at - a.created_at)
                                .map((stamp: LifeStamp, i: number) => ({
                                    note: stamp.note,
                                    ts: stamp.ts,
                                    _key: `list-item-${i}`,
                                    tags: stamp.tags
                                }))
                                .map((values: ListItemT) => <ListItem {...values} key={`${values._key}-container`}/>)
                        }
                    </div>;
                }
            }
        </StampsContext.Consumer>
    );
}

type ListItemT = {
    note: string;
    ts: string;
    _key: string;
    tags?: string[];
}

function ListItem({note, ts, _key, tags}: ListItemT) {
    return <div className="stamp" key={_key}>
        <div className="ts">
            {ts}
        </div>
        <div className="note">
            <ReactMarkdown>{note}</ReactMarkdown>
        </div>
        {tags && tags?.length > 0 &&
        <div className="tags">
            {tags.map((tag: string, index: number) => <span className="tag" key={`key-tag-${index}`}>{tag}</span>)}
        </div>
        }
    </div>;
}
